<!-- eslint-disable max-len -->
<template>
  <li>
    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>

    <ProductCounter class="product__counter form__counter" v-model.number="amount" />

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteFromCart()">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
    <div v-show="productDeleted"> Product deleted from Cart</div>
    <div v-show="productDeletedSending">Product deleting process</div>
  </li>
</template>
<script>
import { mapActions } from 'vuex';
import numberFormat from '../helpers/numberFormat';
import ProductCounter from './ProductCounter.vue';

export default {
  data() {
    return {
      productDeleted: true,
      productDeletedSending: false,
    };
  },
  components: { ProductCounter },
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    // ...mapMutations({ deletProduct: 'deleteCartProduct' }),
    ...mapActions(['deleteCartProduct']),
    deleteFromCart() {
      this.deleteCartProduct({ productId: this.item.productId });
    },
  },

};
</script>
