<script>
import { mapGetters } from 'vuex';
import OrderItemsRight from '../components/OrderItemsRight.vue';
import numberFormat from '../helpers/numberFormat';

export default {
  components: { OrderItemsRight },
  computed: {
    ...mapGetters({ info: 'orderInfo' }),
    totalPriceCount() {
      return this.info.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
  },
  created() {
    if (this.$store.orderInfo && this.$store.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
  filters: {
    numberFormat,
  },
};
</script>
