<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText title="ФИО" :error="formError.name" placeholder="Введите ваше полное имя"
              v-model="formData.name" />

            <BaseFormText title="Адрес доставки" :error="formError.address" placeholder="Введите ваш адрес"
              v-model="formData.address" />

            <BaseFormText title="Телефон" :error="formError.phone" placeholder="Введите ваш телефон"
              v-model="formData.phone" />

            <BaseFormText title="Email" :error="formError.email" placeholder="Введи ваш Email" v-model="formData.email" />

            <BaseFormTextarea title="Комментарии" :error="formError.comment" placeholder="Ваши пожелания к заказу"
              v-model="formData.comment" />

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItemsRight v-for="item in products" :item="item" :key="item.product.id" :title="item.product.title"
              :price="item.product.price" :id="item.product.id" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="productAddSending">
            Оформить заказ
          </button>
          <div v-show="orderSending">Making an order</div>
        </div>
        <div class="cart__error form__error-block" v-if="globalError">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ globalError }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import axios from 'axios';
import { mapGetters } from 'vuex';
import { API_BASE_URL } from '../config';
import BaseFormText from '../components/BaseFormText.vue';
import BaseFormTextarea from '../components/BaseFormTextarea.vue';
import OrderItemsRight from '../components/OrderItemsRight.vue';
import numberFormat from '../helpers/numberFormat';

export default {
  components: { BaseFormText, BaseFormTextarea, OrderItemsRight },
  data() {
    return {
      formData: {
        name: '',
        address: '',
        phone: '',
        email: '',
        comment: '',
      },
      formError: {
        name: '',
        address: '',
        phone: '',
        email: '',
        comment: '',
      },
      globalError: '',
      orderDone: false,
      orderSending: false,
    };
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProduct', totalPrice: 'cartFullPrice' }),
  },
  methods: {
    order() {
      this.formError = {};
      this.orderDone = false;
      this.orderSending = true;
      axios
        .post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          this.products = [];
          this.orderDone = true;
          this.orderSending = false;
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.globalError = error.response.data.error.message;
        });
    },
  },
  filters: {
    numberFormat,
  },
};
</script>
