<template>
  <div>
    <button type="button" aria-label="Убрать один товар" @click="decrement(amount)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" :value="amount" @input="$emit('changeQuantity', $event.target.value)">

    <button type="button" aria-label="Добавить один товар" @click="increment(amount)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>

  </div>
</template>

<script>
export default {
  model: {
    prop: 'amount',
    event: 'changeQuantity',
  },
  props: ['amount'],
  methods: {
    increment(amount) {
      this.$emit('changeQuantity', amount + 1);
    },
    decrement(amount) {
      if (amount <= 1) {
        alert('misstake');
      } else {
        this.$emit('changeQuantity', amount - 1);
      }
    },
  },
};
</script>
