<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceForm" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :color-id.sync="filterColorId" />
      <section class="catalog">

        <img class="center" v-if="productsLoading" src="../../public/img/Hourglass.gif" alt="">
        <div v-if="productsLoadingFail">Ошибка загрузки файлов <button @click.prevent="loadProducts">Попробовать еще
            разок</button></div>

        <ProductList :products="products" />

        <BasePagination v-model="page" :page.sync="page" :count="countProducts" :per-page="productsPerPage" />

      </section>

    </div>
  </main>
</template>
<!-- eslint-disable max-len -->
<script>
import axios from 'axios';
import { API_BASE_URL } from '../config';
import ProductList from '../components/ProductList.vue';
import BasePagination from '../components/BasePagination.vue';
import ProductFilter from '../components/ProductFilter.vue';

export default {

  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceForm: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPerPage: 6,
      productsData: null,

      productsLoading: false,
      productsLoadingFail: false,
    };
  },
  computed: {

    products() {
      return this.productsData ? this.productsData.items.map((product) => ({
        ...product,
        img: product.image.file.url,
      })) : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  /* eslint-disable */
  methods: {
    loadProducts() {
      this.productsLoadingFail = false;
      this.productsLoading = true;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceForm,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            }
          })
          .then(response => (this.productsData = response.data))
          .catch(() => (this.productsLoadingFail = true))
          .then(() => (this.productsLoading = false));
      }, 1000);
    }
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceForm() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
<style>
.center {
  width: 60px;
  height: 100px;
  margin: auto auto;
}
</style>
